import Vue from "vue";

const api = {
  // 首页列表信息
  getHomeData() {
    return Vue.axios.get(
      "http://rap2api.taobao.org/app/mock/85319/api/home/listdata"
    );
  },
  // 图文列表详情页面信息（根据id）来获取
  getListDetails(_id) {
    return Vue.axios.get(
      "http://rap2api.taobao.org/app/mock/85319/api/details",
      { params: { id: _id } }
    );
  },
  // 获取红牛产品轮播图图片
  getProductListImg() {
    return Vue.axios.get(
      "http://rap2api.taobao.org/app/mock/85319/api/product"
    );
  },
  // 获取新闻列表
  getNewsList() {
    return Vue.axios.get("http://rap2api.taobao.org/app/mock/85319/api/news");
  },
  // 获取品牌活动列表 和 轮播图图片
  getActivityList() {
    return Vue.axios.get(
      "http://rap2api.taobao.org/app/mock/85319/api/activity"
    );
  },
  // 获取活动详情图片信息
  getActivityDetails(_id) {
    return Vue.axios.get(
      "http://rap2api.taobao.org/app/mock/85319/api/activity/details",
      { params: { id: _id } }
    );
  }
};

export default api;
