// 下拉导航栏组件

<template>
  <div class="dropdown_bar" :class="{'bar-bg': openBol}">
    <div class="nav clearfix">
        <router-link tag="div" to="/" class="home_logo">
          <span>RedNiu</span>
          <img class="logo" src="../assets/image/logo.png" alt="logo">
          <span>能量饮料</span>  
        </router-link>
        
        <div class="sidebar" @click="switchSideBar()">
          <div class="sidebar_open" v-if="!openBol">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
          <div class="sidebar_close" v-else>
            <span></span>
            <span></span>
          </div>
        </div>
    </div>
    <div class="nav_list" v-show="openBol" @click="switchSideBar()">
      <p class="list_name">发现广州红牛</p>
      <ul>
        <router-link class="nav_li" tag="li" :to="item.url" v-for="(item, index) in navData" :key="index">
          <div class="nav_img">
            <img v-lazy="returnPicUrl(index)" :alt="item.title">
          </div>
          <span class="nav_text">{{item.title}}</span>
        </router-link>  
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "dropdown_bar",
  data() {
    return {
      openBol: false,
      navData: [
        { url: "/about", title: "关于红牛集团" },
        { url: "/product", title: "红牛产品" },
        { url: "/news", title: "新闻快报" },
        { url: "/activity", title: "品牌活动" },
        { url: "/videopage", title: "精彩视频" },
        { url: "/join/0", title: "加入红牛" }
      ]
    };
  },
  methods: {
    // 打开关闭下拉导航栏
    switchSideBar() {
      this.openBol = !this.openBol;
    },
    returnPicUrl(index) {
      return require(`../assets/image/nav${index + 1}.png`);
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
img {
  width: 100%;
}
.bar-bg {
  background-color: #07042f !important;
}
.dropdown_bar {
  background-color: #07042f;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
}
.nav {
  height: 1rem;
  line-height: 1rem;
  color: white;
  font-size: 0.34rem;
  font-weight: bold;
  padding: 0 0.3rem;
  position: relative;
}
.logo {
  width: 0.63rem;
  height: 0.57rem;
  vertical-align: middle;
  margin: -0.1rem 0.12rem 0;
}
.home_logo {
  float: left;
}
.sidebar {
  float: right;
  margin-top: 0.32rem;
  .sidebar_open {
    span {
      display: block;
      width: 0.56rem;
      height: 0.04rem;
      background-color: #fff;
      border-radius: 0.01rem;
      margin-bottom: 0.07rem;
    }
  }
  .sidebar_close {
    position: relative;
    width: 0.24rem;
    height: 0.24rem;
    transform: rotate(45deg);
    span {
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      width: 0.34rem;
      height: 0.04rem;
      background-color: #fff;
      &:first-of-type {
        transform: rotate(90deg);
      }
    }
  }
}

.nav_list {
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: #050322;
  > ul {
    overflow-x: scroll;
    height: 13rem;
  }
  .list_name {
    font-size: 0.3rem;
    color: #999;
    // margin-bottom: 0.2rem;
  }
  .nav_li {
    position: relative;
    margin-top: 0.2rem;
  }
  .nav_text {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
    text-align: center;
    height: 0.3rem;
    font-size: 0.34rem;
    color: #fff;
  }
  .nav_img {
    width: 100%;
    height: 2.4rem;
    overflow: hidden;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: black;
      opacity: 0.5;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}
</style>
