<script>
import DropdownBar from "@/components/DropdownBar";
import FooterBar from "@/components/FooterBar";
import { VueLoading } from "vue-loading-template";
import Api from "../api/index.js";

export default {
  name: "content_detail",
  data() {
    return {
      // 加载动画
      loadBol: true,
      // 默认初始值
      initData: {
        title: "暂无内容",
        author: "",
        pubdate: "",
        content: []
      }
    };
  },
  created() {
    // 接收列表页面传递过来的id
    this.initPage(this.$route.params.id);
  },
  methods: {
    // 根据id请求详情页面数据
    initPage(_id) {
      Api.getListDetails(_id).then(res => {
        let result = res.data;
        if (result.msg_type == 0) {
          this.initData = JSON.parse(JSON.stringify(result.data));
        }
        // 关闭load加载动画
        this.loadBol = false;
      });
    }
  },
  components: {
    DropdownBar,
    FooterBar,
    VueLoading
  }
};
</script>
<style scoped lang="less">
